<template>
  <div id="app" class="p-8 w-3/4 mx-auto">
    <div class="mb-4">
      <AddTaskInput @added="taskAdded" />
    </div>
    <BaseCheckbox
      class="mb-4 p-4 text-gray-600 text-sm font-weight-100"
      v-model="onlyPending"
      >Only pending tasks</BaseCheckbox
    >
    <div class="grid grid-cols-1 gap-4 md:grid-cols-3">
      <TodoListItem
        v-for="task in sortedTasks"
        :task="task"
        v-model:done="task.done"
        v-model:priority="task.priority"
        :key="task.id"
      />
    </div>
  </div>
</template>

<script>
let nextTaskId = 100;

import AddTaskInput from "./components/AddTaskInput.vue";
import BaseCheckbox from "./components/BaseCheckbox.vue";
import TodoListItem from "./components/TodoListItem.vue";

export default {
  name: "App",
  components: {
    AddTaskInput,
    BaseCheckbox,
    TodoListItem,
  },
  data() {
    return {
      tasks: [
        {
          id: 1,
          description: "Buy food for the dog",
          priority: false,
          done: false,
        },
        {
          id: 2,
          description: "Pay the bills",
          priority: true,
          done: false,
        },
        {
          id: 3,
          description: "Buy some computer games",
          priority: false,
          done: false,
        },
        {
          id: 4,
          description: "Go to the gym",
          priority: false,
          done: false,
        },
      ],
      onlyPending: false,
    };
  },
  methods: {
    taskAdded(task) {
      this.tasks.push({
        id: nextTaskId++,
        description: task,
        done: false,
        priority: false,
      });
    },
    taskToggled(task) {
      console.log(`Toggling task ${task.id}`);
      const foundTask = this.tasks.find((t) => t.id === task.id);

      if (foundTask) {
        foundTask.done = task.done;
      }
    },
    taskPrioritized(task) {
      console.log(`Prioritizing task ${task.id}`);
      this.tasks.find((t) => t.id === task.id).priority = task.priority;
    },
  },
  computed: {
    sortedTasks() {
      return [...this.tasks]
        .sort((a, b) => Number(b.priority) - Number(a.priority))
        .filter((task) => !this.onlyPending || !task.done);
    },
  },
};
</script>
